export default {
	data() {
		return {
			// viewerUrl: '/hybrid/html/web/viewer.html',
			viewerUrl:  '/pdf/web/viewer.html',
			allUrl: ''
		}
	},
	onLoad(options) {
		let fileUrl = encodeURIComponent( '/api/attachment?name=' + options.name + '&url=' + options.url)
		this.allUrl = this.viewerUrl + '?file=' + fileUrl
	}
}
